import React, { useEffect, useState, useRef } from "react";
import Matter from "matter-js";

const STATIC_DENSITY = 15;
const PARTICLE_SIZE = 6;
const PARTICLE_BOUNCYNESS = 0.9;
const colors = ["red", "purple", "blue", "green", "pink"];

// buckets
const bucketKeys = ["award 1", "award 2", "award 3", "award 4"];

const randomBetween = (min, max) =>
  Math.floor(Math.random() * (max - min)) + min;

// global variables used throughout the canvas
const width = 800;
const height = 600;
// number of columns and rows for the grid of pegs
const columns = 20;
const rows = 10;
// margin to allocate buckets underneath the grid
const margin = 100;
// padding to include make space for two rectangles at either side of the canvas
const padding = width / columns / 2;

export const MatterStepThree = () => {
  const boxRef = useRef(null);
  const canvasRef = useRef(null);

  // const [constraints, setContraints] = useState();
  const [scene, setScene] = useState();
  const [award, setAward] = useState();

  const [someStateValue, setSomeStateValue] = useState(false);

  // const handleResize = () => {
  //   setContraints(boxRef.current.getBoundingClientRect());
  // };

  const handleClick = () => {
    setSomeStateValue(!someStateValue);
  };

  useEffect(() => {
    let Engine = Matter.Engine;
    let Render = Matter.Render;
    let World = Matter.World;
    let Bodies = Matter.Bodies;

    let engine = Engine.create({});

    let render = Render.create({
      element: boxRef.current,
      engine: engine,
      canvas: canvasRef.current,
      options: {
        background: "transparent",
        wireframes: false,
      },
    });

    const floor = Bodies.rectangle(0, 0, 0, STATIC_DENSITY, {
      isStatic: true,
      render: {
        fillStyle: "blue",
      },
    });

    // function fabricating a peg, as a white circle with a static position
    // accepting as input the coordinates of the circle's center
    const makePeg = (x, y) => {
      const r = 5;

      return Bodies.circle(x, y, r, {
        isStatic: true,
        render: { fillStyle: "black" },
        // add a label to later identify the circle in the collision event
        label: "peg",
      });
    };

    // function fabricating a bucket, as a white taller-than-wider rectangle positioned at the bottom of the canvas
    const makeBucket = (x) => {
      const w = 5;
      const h = 80;
      const y = height - h / 2;

      return Bodies.rectangle(x, y, w, h, {
        isStatic: true,
        render: {
          fillStyle: "black",
        },
      });
    };

    // PLINKO's elements
    // to prevent the plinkos from bouncing off the canvas's scope, include rectangle elements at the bottom and sides of the element
    const contourSize = 50;
    const contourBottom = Bodies.rectangle(
      width / 2,
      height + contourSize / 2,
      width,
      contourSize,
      {
        isStatic: true,
      }
    );

    // use the padding to create the left and right walls as white tall and thin rectangles
    // the idea is to have them pick up the color of the plinkos coming into contact with the shape
    const contourLeft = Bodies.rectangle(0, 0, padding / 2, height * 2, {
      isStatic: true,
      render: {
        fillStyle: "white",
      },
    });
    const contourRight = Bodies.rectangle(width, 0, padding / 2, height * 2, {
      isStatic: true,
      render: {
        fillStyle: "white",
      },
    });

    const contours = [contourLeft, contourRight];
    // pegs
    // array of columns and rows; the idea is to have pegs ranging the width and height of the canvas
    // use the padding to reduce the horizontal space allocated to the pegs
    const columnSize = (width - padding * 2) / columns;
    const rowSize = (height - margin) / rows;
    const grid = Array(rows)
      .fill()
      .map((rowItem, row) => {
        // have the number of columns alternate with a shorter row every other row
        // the idea is to then offset the shorter row to recreate the plinko's own structure
        const cols = row % 2 === 0 ? columns : columns - 1;
        const dx = cols !== columns ? columnSize / 2 : 0;
        return Array(cols)
          .fill()
          .map((columnItem, column) => {
            const x = padding + columnSize * column + columnSize / 2 + dx;
            const y = rowSize * row + rowSize / 2;
            return makePeg(x, y);
          });
      });
    // since grid refers to a 2d array, flatten the items to a one-dimensional array
    const pegs = grid.reduce((acc, curr) => [...acc, ...curr], []);

    // buckets
    // arrays of buckets in which the plinkos would eventually fall
    // as many buckets as the number of columns -1, to make use of the left and right wall for the outer edges
    const buckets = Array(bucketKeys.length - 1)
      .fill()
      .map((columnItem, column) => {
        const columnSize = width / bucketKeys.length;
        const x = columnSize * column + columnSize;
        console.log("bucket", x);
        return makeBucket(x);
      });

    const awards = bucketKeys.map((columnItem, column) => {
      const w = width / bucketKeys.length;
      const h = 40;
      const x = column * w + w / 2;
      const y = height - h / 2 + 20;

      const fillStyle = `hsl(${randomBetween(0, 360)}, 90%, 60%)`;

      return Bodies.rectangle(x, y, w, h, {
        isStatic: true,
        render: {
          fillStyle,
        },
        id: columnItem,
        label: "award",
      });
    });
    World.add(engine.world, [...contours, ...pegs, ...buckets, ...awards]);

    Engine.run(engine);
    Render.run(render);

    // setContraints(boxRef.current.getBoundingClientRect());
    setScene(render);

    function handleCollision(event) {
      const { pairs } = event;

      pairs.forEach((pair) => {
        const { bodyA, bodyB } = pair;
        const { label: labelA } = bodyA;
        const { label: labelB } = bodyB;

        let ball = undefined;
        let other = undefined;
        if (labelA === "ball") {
          ball = bodyA;
          other = bodyB;
        }
        if (labelB === "ball") {
          ball = bodyB;
          other = bodyA;
        }

        if (other.label === "award") {
          // alert("victory: " + other.id);
          setAward(other.id);
        }
        if (other.label === "peg") {
          other.render.fillStyle = ball.render.fillStyle;
        }
      });
    }
    Matter.Events.on(engine, "collisionStart", handleCollision);

    // window.addEventListener("resize", handleResize);
  }, []);

  useEffect(() => {
    return () => {
      // window.removeEventListener("resize", handleResize);
    };
  }, []);

  // useEffect(() => {
  //   // let { width, height } = constraints;

  //   // Dynamically update canvas and bounds
  //   scene.bounds.max.x = width;
  //   scene.bounds.max.y = height;
  //   scene.options.width = width;
  //   scene.options.height = height;
  //   scene.canvas.width = width;
  //   scene.canvas.height = height;

  //   // Dynamically update floor
  //   const floor = scene.engine.world.bodies[0];

  //   Matter.Body.setPosition(floor, {
  //     x: width / 2,
  //     y: height + STATIC_DENSITY / 2,
  //   });

  //   Matter.Body.setVertices(floor, [
  //     { x: 0, y: height },
  //     { x: width, y: height },
  //     { x: width, y: height + STATIC_DENSITY },
  //     { x: 0, y: height + STATIC_DENSITY },
  //   ]);
  // }, [scene]);

  /*eslint-disable */
  useEffect(() => {
    // Add a new "ball" everytime `someStateValue` changes
    if (scene) {
      // let { width } = constraints;
      let randomX = Math.floor(Math.random() * -width) + width;
      Matter.World.add(
        scene.engine.world,
        Matter.Bodies.circle(randomX, -PARTICLE_SIZE, PARTICLE_SIZE, {
          restitution: PARTICLE_BOUNCYNESS,
          label: "ball",
        })
      );
    }
  }, [scene, someStateValue]);
  /*eslint-enable */

  return (
    <div
      style={{
        position: "relative",
        border: "1px solid white",
        padding: "8px",
      }}
    >
      <button
        style={{
          cursor: "pointer",
          display: "block",
          textAlign: "center",
          marginBottom: "16px",
          width: "100%",
        }}
        onClick={() => handleClick()}
      >
        Release the ball!
      </button>

      <div
        ref={boxRef}
        style={{
          minHeight: "400px",
          width: "100%",
          height: "100%",
          pointerEvents: "none",
        }}
      >
        <canvas ref={canvasRef} />
      </div>
      <div
        style={{
          display: "flex",
          "justify-content": "space-between",
          width: "800px",
        }}
      >
        {bucketKeys.map((key) => (
          <span>{key}</span>
        ))}
      </div>
      <div>
        {award ? `you won... ${award}` : "drop a ball and see what you win"}
      </div>
    </div>
  );
};
